<template>
    <div class="modal-window" v-if="modalStatus && !isAuth">
        <div class="modal-close" v-on:click="closeModal"></div>
        <div class="modal-content ellipse ellipse-after" v-if="url === 'registration'">
            <h1 class="modal-title"> </h1>
            <span class="modal-subtitle">  ? <router-link to="?page=login" class="ease"></router-link></span>
            <form v-on:submit.prevent="registration" class="form" autocomplete="off">
                <div class="row">
                    <div class="col-12 col-md-6">
                        <label class="form-label"></label>
                        <div class="form-group">
                            <input type="text" class="form-input" v-model="firstname" placeholder="" :disabled="loading" required>
                        </div>
                    </div>
                    <div class="col-12 col-md-6">
                        <label class="form-label"></label>
                        <div class="form-group">
                            <input type="text" class="form-input" v-model="lastname" placeholder="" :disabled="loading" required>
                        </div>
                    </div>
                </div>
                <label class="form-label">E-mail</label>
                <div class="form-group">
                    <input type="email" class="form-input" v-model="email" placeholder=" e-mail" :disabled="loading" required>
                </div>
                <label class="form-label"></label>
                <div class="form-group">
                    <input type="password" class="form-input" v-model="password" placeholder=" " :disabled="loading" required>
                </div>
                <div class="form-group">
                    <input type="password" class="form-input" v-model="passwordRepeat" placeholder=" " :disabled="loading" required>
                </div>
                <span class="form-agree">   ,    <router-link to="/rules"> </router-link></span>
                <button class="button block lg" type="submit" :disabled="loading"></button>
            </form>
        </div>
        <div class="modal-content ellipse ellipse-after" v-if="url === 'login'">
            <h1 class="modal-title">   </h1>
            <span class="modal-subtitle">  ? <router-link to="?page=registration" class="ease"></router-link></span>
            <form v-on:submit.prevent="login" class="form" autocomplete="off">
                <label class="form-label">E-mail</label>
                <div class="form-group">
                    <input type="email" class="form-input" v-model="email" placeholder=" e-mail" :disabled="loading" required>
                </div>
                <label class="form-label"></label>
                <div class="form-group">
                    <input type="password" class="form-input" v-model="password" placeholder=" " :disabled="loading" required>
                </div>
                <template v-if="twofactor">
                    <label class="form-label">Google 2Fa Authenticator</label>
                    <div class="form-group">
                        <input type="number" class="form-input" ref="code" placeholder="  " :disabled="loading" required>
                    </div>
                </template>
                <span class="form-agree text-right"><router-link to="?page=restore"> ?</router-link></span>
                <button class="button block lg" type="submit" :disabled="loading">   </button>
            </form>
        </div>
        <div class="modal-content ellipse ellipse-after" v-if="url === 'activation'">
            <h1 class="modal-title text-center"> </h1>
            <span class="modal-subtitle text-center">      . ,     </span>
            <form v-on:submit.prevent="activation" class="form" autocomplete="off">
                <label class="form-label">E-mail</label>
                <div class="form-group">
                    <input type="email" class="form-input" disabled :value="email">
                </div>
                <label class="form-label"> </label>
                <div class="form-group">
                    <input type="number" class="form-input" ref="code" placeholder="  " :disabled="loading" required>
                </div>
                <button class="button block lg mt-4" type="submit" :disabled="loading"></button>
                <span class="modal-subtitle text-center mt-4 mb-0">  ? <a class="cursor" :disabled="loading" v-on:click="resend">  </a></span>
            </form>
        </div>
        <div class="modal-content ellipse ellipse-after" v-if="url === 'restore'">
            <h1 class="modal-title text-center"> ?</h1>
            <span class="modal-subtitle text-center">  E-mail,    </span>
            <form v-on:submit.prevent="restore" class="form" autocomplete="off">
                <label class="form-label">E-mail</label>
                <div class="form-group">
                    <input type="email" class="form-input" v-model="email" placeholder=" e-mail" :disabled="loading" required>
                </div>
                <button class="button block lg mt-4" type="submit" :disabled="loading"> </button>
                <span class="modal-subtitle text-center mt-4 mb-0"> ? <router-link to="?page=login">  </router-link></span>
            </form>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            url: '',
            modalStatus: false,
            firstname: '',
            lastname: '',
            email: '',
            password: '',
            passwordRepeat: '',
            isAuth: false
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        twofactor() {
            return this.$store.getters.twofactor
        }
    },
    watch: {
        $route() {
            this.showModal()
            this.email = this.$route.query['email']
            if(this.$route.query['AccessDenied']) {
                this.$store.dispatch('notification', {message: 'Please log in to access'})
            }
            if(this.$route.query['SessionExpired']) {
                this.$store.dispatch('notification', {message: '  . ,  ,   .'})
            }
        }
    },
    methods: {
        showModal() {
            if (this.$route.query['page'] && !localStorage.getItem('isAuth') && !localStorage.getItem('token')) {
                this.$store.dispatch('twofactor', false)
                this.modalStatus = true
                this.url = this.$route.query['page']
            } else {
                this.modalStatus = false
            }
        },
        closeModal() {
            this.modalStatus = false
            this.url = ''
            this.$router.replace({ 'query': null })
        },
        notification(message, type) {
            this.$store.dispatch('notification', { message, type })
        },
        registration() {
            if (!this.firstname || this.firstname.trim() === '') {
                this.notification(' ')
            } else {
                if (!this.lastname || this.lastname.trim() === '') {
                    this.notification(' ')
                } else {
                    if (!this.email || this.email.trim() === '') {
                        this.notification(' E-mail')
                    } else {
                        if (!this.password || this.password.trim() === '') {
                            this.notification(' ')
                        } else {
                            if (!this.passwordRepeat || this.passwordRepeat.trim() === '') {
                                this.notification(' ')
                            } else {
                                if (this.passwordRepeat !== this.password) {
                                    this.notification('  ')
                                } else {
                                    const data = {
                                        'firstname': this.firstname,
                                        'lastname': this.lastname,
                                        'email': this.email,
                                        'password': this.password,
                                        'password_confirm': this.passwordRepeat
                                    }
                                    this.$store.dispatch('registration', data)
                                }
                            }
                        }
                    }
                }
            }
        },
        activation() {
            if (!this.$refs.code.value || String(this.$refs.code.value).trim() === '') {
                this.notification('  ')
            } else {
                const data = {
                    'email': this.$route.query['email'],
                    'code': this.$refs.code.value
                }
                this.$store.dispatch('activation', data)
            }
        },
        resend() {
            if (!this.$route.query['email'] || this.$route.query['email'] === '') {
                this.notification(' E-mail')
            } else {
                this.$store.dispatch('resend', {email: this.$route.query['email']})
            }
        },
        login() {
            if (!this.email || this.email.trim() === '') {
                this.notification(' E-mail')
            } else {
                if (!this.password || this.password.trim() === '') {
                    this.notification(' ')
                } else {
                    const data = {
                        'email': this.email,
                        'password': this.password,
                    }
                    if(this.twofactor) {
                        data['2fa_otp'] = String(this.$refs.code.value)
                    }
                    this.$store.dispatch('login', data)
                }
            }
        },
        restore() {
            if (!this.email || this.email.trim() === '') {
                this.notification(' E-mail')
            } else {
                this.$store.dispatch('restore', {email: this.email})
            }
        }
    }
}
</script>